@import url(http://fonts.cdnfonts.com/css/roboto);

*{
  padding: 0;
  margin:0;
  
}

body, html,#root,.App {
  height: 100%;
  width: 100%;
  font-family: Roboto, serif;
}
.App{
  display: grid;
  grid-template-rows: 80px 1fr;
  grid-template-columns: 340px 1fr;
 
}


.contentContainer{
  grid-column: 2;
  grid-row: 2;
  padding-top:81px ;
  overflow-x: auto;
  box-sizing: border-box;
}
@media screen and  (max-width: 768px){
  .App{
    display: block;
    height:100%;
    width: 100%;
    overflow: auto;
    position: relative;
    -ms-scroll-snap-type: y proximity;
        scroll-snap-type: y proximity;
  }
  .snap-scroll{
    scroll-snap-align: start end;
    scroll-margin-top:110px;
  }
  .snap-top-mob_nav{
    scroll-snap-align: start end;
    scroll-margin-top:76px;
  }

}
.SideBar {
    position: fixed;
    z-index: 2;
    background-color: #241F27;
    width: 340px;
    height: 100vh;
    padding: 5px 20px 5px 10px;
    box-sizing: border-box;
    display: grid;
    justify-items: center;
    overflow: hidden;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
    grid-column: 1;
    grid-row: 1 / 3;
    grid-template-rows: 230px 50px auto auto;
    grid-gap: 5px;
    gap: 5px;
}

.pic {
    width: 230px;
    height: 230px;
    border-radius: 50%;
    background-color: aliceblue;
    background-repeat: repeat;
    background-image: url(/resume/static/media/my_pic.7e7fb9b6.jpg);
    background-size: 112%;
    background-position-y: -3px;
    background-position-x: -19px;
    grid-gap: 44px;
    grid-gap: 44px;
    gap: 44px;
}

