@media screen and  (max-width: 768px) {
    .Skills {

        margin: 10px 20px 0px 20px;

    }

    .SideBar::-webkit-scrollbar {
        width: 0 !important
    }

    .SideBar {
        overflow: -moz-scrollbars-none;
    }

    .mobileNav {
        padding: 5px 10px;
        z-index: 400;
        background-color: #241F27;
        display: grid;
        grid-template-columns:100px auto;
        height: 110px;
        position: fixed;
        grid-column-gap: 5px;
        -webkit-column-gap: 5px;
           -moz-column-gap: 5px;
                column-gap: 5px;
        width: 100%;
        box-sizing: border-box;
    }

    .mini_nav {
        height: 76px;
        grid-template-columns: 70px auto;
        justify-items: center;
        align-items: center;

    }

    .mini_nav .pic {
        width: 64px;
        height: 64px;
        background-position-y: -5px;
        background-position-x: -5px;
        background-size: 120%;

    }

    .mini_nav > .Informations {
        display: none;
    }

    .mini_nav > #Name {
        align-self: center;
        grid-column: 2;
        grid-row: 1/3;
    }

    .SideBar {
        position: relative;
        margin-top: 110px;
        background-color: #49404E;
        width: 100%;
        height: calc(100vh - 110px);
        padding: 0 10px;
        box-sizing: border-box;
        display: grid;
        box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
        grid-column-gap: 5px;
        -webkit-column-gap: 5px;
           -moz-column-gap: 5px;
                column-gap: 5px;
        grid-template-rows: auto;
        position: static;
        overflow: scroll;
    }

    .pic {
        grid-column: 1;
        grid-row: 1/3;
        width: 100px;
        height: 100px;
        align-self: center;

        border-radius: 50%;
        background-repeat: repeat;
        background-image: url(/resume/static/media/my_pic.7e7fb9b6.jpg);
        background-size: 112%;
        background-position-y: -1px;
        background-position-x: -7px;
        grid-gap: 44px;
    }

    .Informations {
        grid-column: 2;
        grid-row: 2;
        display: grid;
        justify-self: start;
        grid-template-columns: 20px auto;
        grid-column-gap: 0;
        -webkit-column-gap: 0;
           -moz-column-gap: 0;
                column-gap: 0;
        justify-content: center;
        align-items: center;
        height: 80px;
    }

    .InfoText {
        /*font-family: Roboto;*/
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 12px;
    }

    #Name {
        align-self: end;
        justify-self: start;
        font-style: normal;
        font-weight: bold;
        font-size: 18px;
    }

    #mail_icon {
        width: 14px;
        height: 11px;
    }

    #location_icon {
        width: 10px;
        height: 14px;
    }

    #calander_icon {
        width: 12px;
        height: 14px;
    }

    #phone_icon {
        width: 9px;
        height: 15px;
    }

    h1.skill_name {

        font-weight: 500;
        font-size: 14px;
        line-height: 16px;

    }

    .logo_container {
        justify-self: center;
        align-self: start;
    }

    .Skills {
        grid-gap: 5px;
        gap: 5px;
        margin-top: 10px;
    }


}

